* {
  margin: 0;
  padding: 0;
}
html, body {
  height: 100%;
}
#root {
  height: 100%;
  .cont {
    padding-left: 80px;
  }
}
ul {
  list-style: none;
}
h1 {
  flex: 0 0 44px;
  background-color: #000000;
  color: #fff;
  padding-left: 50px;
  margin: 0;
  >span {
    padding-left: 20px;
    font-size: 16px;
  }
  .right {
    float: right;
    margin-right: 50px;
    font-size: 14px;
    line-height: 40px;
    a {
      color: white;
      padding-right: 16px;
      &:hover {
        color: #ffd867;
      }
    }
  }
}
.main {
  display: flex;
  flex-flow: column;
  height: 100%;
  >.cont {
    flex: auto;
    overflow-y: auto;
  }
}